import React from 'react';
import { makeStyles } from '@material-ui/core';
import MaterialsList from '../List';
import FAIcon from '../../components/general/FAIcon';
import { useSelector } from 'react-redux';
import { materialTypeMatch, getMaterialId } from '../utils';
import classNames from 'classnames';
import MyPopover from '../../components/general/Popover';
import { materialsSelector, materialTypesSelector } from '../../../utils/redux/config';

const useStyles = makeStyles(theme => ({
  tagContent: {
    width: 'fit-content',
    margin: '0 auto',
    marginTop: '7px',
  },
  perfectScore: {
    color: theme.palette.secondary.main,
  },
  closeScore: {
    color: theme.palette.primary.main,
  }
}));

const usePopoverStyles = makeStyles(theme => ({
  paper: {
    padding: theme.spacing(2),
    width: '160px',
    maxHeight: '200px',
    overflowY: 'scroll',
  },
}))
export default function MaterialsTag({activity, className}: {activity: any, className: string}){
  const materials = useSelector(materialsSelector);
  const materialTypes = useSelector(materialTypesSelector);

  const score = activity?.properties?.metaRequirements.reduce((acc: number, req: any)=>{
    return acc + Number(!(materials[getMaterialId(activity.id, req)] || materialTypeMatch(materialTypes)(req.types) || false));
  }, 0);
  
  
  const classes = useStyles();
  const popoverClasses = usePopoverStyles()
  
  return <MyPopover
    className={className}
    popoverClasses={popoverClasses}
    renderHoverableContent={()=><div
      className={classes.tagContent}
      >
      <FAIcon className={classNames({
        [classes.perfectScore]: !score,
        [classes.closeScore]: score > 0 && score < 4
      })} size="2x" raw={classNames({
        'las la-list-alt': !score,
        'las la-list': score > 3,
        'las la-tasks': score > 0 && score < 4
      })} /></div>}
    renderOnHoverContent={()=><MaterialsList activity={activity} />}
    popoverProps={
      {
        anchorOrigin: {
          vertical: 'bottom',
          horizontal: 'left',
        },
        transformOrigin: {
          vertical: 'bottom',
          horizontal: 'left',
        }
      }
    }
  />
}

