import React, { useState } from 'react';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { v4 } from 'uuid';
import * as yup from 'yup';
import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers';

import { SERVER_ADDRESS, post } from '../../../utils/remote';
import { setToken } from '../../../utils/auth';
import { decode } from 'jsonwebtoken';
import { useHistory } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { createUserAction } from '../../../utils/redux/user';
export const endpoint = SERVER_ADDRESS+'/privateUsers';
export function UserCreate() {
  const [id, updateId] = useState(v4())
  return (
    <div>
      <Typography variant="h1">Signup</Typography>
      <UserCreateForm
        key={id}
        id={id}
        // reset={()=>updateId(v4())}
      />
    </div>
  );
}

const schema = yup.object().shape({
  // username: yup.string().matches(/^[a-z0-9]+$/i, 'Only alphanumeric characters').required(),
  email: yup.string().email().required(),
  password: yup.string().min(6).max(122).required(),
});

const UserCreateForm = ({id}: {id: string})=>{
  const { handleSubmit, register, errors } = useForm({
    resolver: yupResolver(schema),
  });
  const history = useHistory();
  const dispatch = useDispatch();
  const onSubmit = (data: any) => {
    post(endpoint, {
      body:
        {
          id: v4(),
          // username: data.username,
          email: data.email,
          password: data.password,
        }
    }).then((resp)=>{
      return resp.json()
    }).then((res)=>{
      setToken(res.data);
      dispatch(createUserAction(decode(res.data)));
      history.push('/');
    });
  }
  return <form
      onSubmit={handleSubmit(onSubmit)}
    >
      {
        // <div>
        //   <TextField
        //     inputRef={register}
        //     label="Username"
        //     name='username'
        //     error={!!errors.username}
        //     helperText={errors.username?.message}
        //   />
        // </div>
      }
    <div>
      <TextField
        id="email"
        name="email"
        inputRef={register}
        label="Email"
        error={!!errors.email}
        helperText={errors.email?.message}
      />
    </div>
    <div>
      <TextField 
        inputRef={register}
        id="password"
        name="password"
        label="Password"
        type="password"
        error={!!errors.password}
        helperText={errors.password?.message}
      />
    </div>
    <Button type="submit">Signup</Button>
  </form>
}