import React from 'react';
import { Fade } from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css'

const hidden = <div style={{display: 'none'}}/>

type $props = {
  images: any[]
}

export default function Slideshow({images}: $props){
    return (
      <Fade 
      prevArrow={hidden}
      nextArrow={hidden}
      autoplay
      duration={500}
      pauseOnHover={false}
      infinite
      easing="ease"
      >
      {
        images//.map((img, i) => <div className="each-slide" key={i}>{img}</div>)
      }
      </Fade>
    )
}