import React from 'react';
import { Card, makeStyles, CardMedia } from '@material-ui/core';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import Tags from '../Tags';
import {
    addedActivitySelector,
    wontDoActivitySelector,
    completedActivitySelector
} from '../../../../../utils/redux/config';

const useStyles = makeStyles((theme)=>({
  root: {
    display: "inline-block",
    width: "170px",
    verticalAlign: 'top',
    marginRight: '1rem',
    marginBottom: '1rem',
  },
  img: {
    height: '350px'
  }
}));

type $props = {
  shedule: any,
  addedActivityPage?: Boolean
}

const PortraitSlot = React.memo(function PortraitSlot(props: $props){
  const activity = props.shedule;
  const addedActivity = useSelector(addedActivitySelector(activity.id));
  const wontDoActivity = useSelector(wontDoActivitySelector(activity.id));
  const completedActivity = useSelector(completedActivitySelector(activity.id));
  
  const classes = useStyles();
  // const popoverClasses = usePopoverStyles();
  if(completedActivity || wontDoActivity) return null;
  if(!props.addedActivityPage && addedActivity) return null;
  
  const activityLinkTo = "/shedules/activities/"+activity.id;
  return <Card key={activity.id} className={classes.root}>
    <Link to={activityLinkTo}>
      <CardMedia title={activity.name} className={classes.img} image={activity.properties.imgLinks.find((img: any) => (img.height / 1.75) > img.width).src}/>
    </Link>
    <Tags activity={activity} />
  </Card>     
});


export default PortraitSlot