import React, { useState, useEffect } from 'react';
import { TextField } from '@material-ui/core';
import { debounce, partition } from 'lodash';

import _ from 'lodash';

import { useCustomSwr, SERVER_ADDRESS } from '../../../../utils/remote';
import SheduleRow from '../../../components/general/SheduleRow';
import Autocomplete, {createFilterOptions} from '@material-ui/lab/Autocomplete';

type $props = {
  popularTags: any[],
  wrap?: Boolean,
}

export default function SearchActivitiesRow(props: $props){
  const [ previousSizes, setPreviousSizes ] = useState({});
  const [ reset, setReset ] = useState(1);
  const [searchTags, setSearchTags] = useState(localStorage.getItem('searchTags') || '#age4 #indoor');
  const [searchText, setSearchText] = useState(localStorage.getItem('searchText') || '');
  const { data, page, resetPage } = useCustomSwr(SERVER_ADDRESS+`/shedules/activities/public`, { 
    defaultValue: [],
    page: true,
    query: {
      tags: searchTags,
      text: searchText,
    }, 
    deps: [searchTags, searchText],
    cache: true
  });
  useEffect(()=>{
    setPreviousSizes({
      ...previousSizes,
      [page]: data.length
    });
  }, [data])
  const onSearchFunc = (nextVal: string)=>{
    const clean = nextVal.toLowerCase().split(' ').filter(v=>v);
    const [nextSeachTagsArr, nextSearchTextArr] = partition(clean, (name)=>name[0] === '#')
    const nextSearchTags = nextSeachTagsArr.join(' ');
    const nextSearchText = nextSearchTextArr.join(' ');
    localStorage.setItem('searchTags', nextSearchTags);
    localStorage.setItem('searchText', nextSearchText);
    setSearchTags(nextSearchTags);
    setSearchText(nextSearchText);
    resetPage();
  }
  const onSearch = debounce(onSearchFunc, 400);
  const filter = createFilterOptions();

  return <div>
    <Autocomplete
      fullWidth
      freeSolo
      key={reset}
      multiple
      id="tags-standard"
      options={(props.popularTags || []).map(t => '#'+t)
        // .concat(searchText.split(' '))
      }
      defaultValue={searchTags.split(' ')}
      onChange={(event, value: any[])=>{
        onSearch(value.join(' '))
      }}
      filterOptions={(options, params) => {
        const filtered : any = filter(options, params);
        if (params.inputValue !== '') {
          filtered.push(`${params.inputValue}`);
        }
        return filtered;
      }}
      selectOnFocus
      clearOnBlur
      handleHomeEndKeys
      renderInput={(params: any) => (
        <TextField
          {...params}
          variant="standard"
          label="Search"
          placeholder="#stem"
        />
      )}
    />
    {
      !data.length ? <p>No Results</p> : <SheduleRow data={data} wrap={props.wrap} />
    }
  </div>
}


