import React, { cloneElement } from 'react';
import { Snackbar, IconButton } from '@material-ui/core';
import { Close as CloseIcon } from '@material-ui/icons';

import { UserCreate } from '../../../users/Create';
import { Switch, Route, useHistory } from 'react-router-dom';
import { SessionCreate } from '../../../sessions/Create';
import SearchActivities from '../../../pages/Search';
import Dashboard from '../../../pages/Dashboard';
import ActivityShow from '../../../shedules/activities/Show';


import { useSelector, useDispatch } from 'react-redux';
import { v4 } from 'uuid';
import ShedulesAddedActivitiesShowMany from '../../../shedules/activities/added/ShowMany';
import Contact from '../../../pages/Contact';
import { userSelector } from '../../../../utils/redux/user';
import { createGlobalAction, globalSelector } from '../../../../utils/redux/global';



const SHEDULES = '/shedules';

export default function MainRoutes() {
  const dispatch = useDispatch();
  const {id, message} = useSelector(globalSelector).snackbar
  const handleClose = ()=>dispatch({
    snackbar: ''
  });
  return <>
    <Snackbar
      key={id}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'left',
      }}
      open={!!id}
      autoHideDuration={6000}
      onClose={handleClose}
      action={
        <IconButton size="small" aria-label="close" color="inherit" onClick={handleClose}>
          <CloseIcon fontSize="small" />
        </IconButton>
      }
      message={message}
    />
  <Switch>
    {
      <Route exact path="/search"><MustBeLoggedIn><SearchActivities /></MustBeLoggedIn></Route>
    }
    
    <Route exact path="/contact"><Contact/></Route>
    <Route exact path="/"><MustBeLoggedIn><Dashboard /></MustBeLoggedIn></Route>
    {
      // <Route exact path="/config"><MustBeLoggedIn><UsersEdit /></MustBeLoggedIn></Route>
    }
    <Route exact path={`/activities/added`}><MustBeLoggedIn><ShedulesAddedActivitiesShowMany /></MustBeLoggedIn></Route>
    <Route exact path={`${SHEDULES}/activities/:sheduleId`}><MustBeLoggedIn><ActivityShow /></MustBeLoggedIn></Route>
    <Route exact path={`/signup`}><UserCreate/></Route>
    <Route exact path={`/login`}><SessionCreate/></Route>
  </Switch>
  </>
};


function MustBeLoggedIn({children, ...props}: {children: JSX.Element}){
  const history = useHistory();
  const user = useSelector(userSelector);
  if(!user){
    history.push('/overview')
  }
  return <>    
    {
      cloneElement(children, props)
    }
  </>
}

