import React from 'react';
import { Tabs, Tab } from '@material-ui/core';

function a11yProps(index: any) {
  return {
    id: `tab-${index}`,
    'aria-controls': `tabpanel-${index}`,
  };
}

export function TabPanel(props: any) {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`tabpanel-${index}`}
      aria-labelledby={`tab-${index}`}
      {...other}
    >
      {value === index && children}
    </div>
  );
}

interface $tabProps {
  label: JSX.Element | string
}

interface $props {
  value: number,
  onChange: (a: number)=>void,
  tabsProps?: any,
  tabPropsArr: $tabProps[]
}

export default function MyTabs({ value, onChange, tabsProps = {}, tabPropsArr = []} : $props){
  return <Tabs 
    value={value}
    onChange={(e, newValue)=>{
      onChange(newValue)
    }}
    aria-label="Change dashboard view"
    indicatorColor="primary"
    textColor="primary"
    variant="fullWidth"
    {...tabsProps}
  >
  {
    tabPropsArr.map((tabProps, i) => <Tab key={i} {...tabProps} {...a11yProps(i)} /> )
  }
  </Tabs>
}
