import React from 'react';
import { Popover } from '@material-ui/core';
import { debounce } from 'lodash';

type $props = {
  disableHover?: Boolean,
  className?: string,
  renderHoverableContent: Function,
  renderOnHoverContent: Function,
  popoverProps?: Object,
  popoverClasses?: Object
}

export default function MyPopover({disableHover, className, popoverClasses, renderHoverableContent, renderOnHoverContent, popoverProps={}} : $props){
  const [mouseStopped, setMouseStopped] = React.useState(false);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [candidateAnchorEl, setCandidateAnchorEl] = React.useState(null);
  const debouncedSetMouseStopped = debounce(()=>setMouseStopped(true), 500);
  React.useEffect(()=>{
    if(candidateAnchorEl && mouseStopped){
      setAnchorEl(candidateAnchorEl);
    }
  }, [candidateAnchorEl, mouseStopped])
  
  const handleHide = ()=>{
    setCandidateAnchorEl(null);
    setAnchorEl(null);
    setMouseStopped(false)
  }
  if(disableHover)
    return <div className={className}>{renderHoverableContent()}</div>
  return <div className={className}
    onMouseEnter={(event: any)=>{
      setCandidateAnchorEl(event.currentTarget)
    }}
    onMouseMove={()=>{
      debouncedSetMouseStopped()
    }}
    onMouseLeave={handleHide}
  >
    {
      renderHoverableContent()
    }
    <Popover
        classes={popoverClasses}
        open={Boolean(anchorEl)}
        anchorEl={anchorEl}
        {...popoverProps}
        onClose={handleHide}
        disableRestoreFocus
      >
        <div
        onMouseLeave={handleHide}
        >
          {
            renderOnHoverContent()
          }
        </div>
      </Popover>
  </div>
}

