import React from 'react';
import { useCustomSwr, SERVER_ADDRESS } from '../../../../utils/remote';

import { useParams, Link } from 'react-router-dom';
import { Typography, makeStyles, Chip, Grid, Card, Box } from '@material-ui/core';
import ReactMarkdown from 'react-markdown';

import humanizeDuration from 'humanize-duration';

import AddActivityButtons from '../added/Button';
import ActivitiesPreviewImages from '../preview/Images';
import MaterialsList from '../../../materials/List';

import { FormLabel } from '@material-ui/core';
import { blue } from '@material-ui/core/colors';


const useStyle = makeStyles({
  spacer: {
    marginTop: '2rem',
  },
  // image: {
  //   width: '100%',
  //   display: 'block',
  //   margin: '2rem auto'
  // },
  tags: {
    margin: '1rem auto 0',
    textAlign: 'center',
  },
  requirements: {
    padding: '0 2rem',
  },
  card: {
    minWidth: '25rem',
    margin: 'auto'
  },
  cardTitle: {
    color: blue[700],
    textDecoration: 'none',
  },
  cardTitleWrapper: {
    margin: '0 1rem'
  },
})

interface $shedulesParams {
  sheduleId: string
}

export default function ActivityShow(props: any){
  const classes = useStyle();
  const { sheduleId } = useParams<$shedulesParams>();
  const { data } = useCustomSwr(SERVER_ADDRESS+'/shedules/activities/public/'+sheduleId);

  
  if(!data) return null;
  const linkWrapper = (children: JSX.Element)=>data?.properties?.link ? <Link className={classes.cardTitle} to={{pathname: data.properties.link}} target="_blank" children={children}/> : <React.Fragment children={children}/>
  return <div>
    <Card className={classes.card}>
      <Grid justify="flex-start" container>
        <Grid item>
          {
            // <LikeShow sheduleId={sheduleId} />
          }
          <AddActivityButtons activity={data} />
        </Grid>
      </Grid>
      <Grid container justify="center">
          <Grid item md={6} lg={6} xl={6}>
            <div className={classes.cardTitleWrapper}>
            {
              linkWrapper(<Typography className={classes.cardTitle} align="center" variant="h5" component="h1">{data.name}</Typography>)
            }
            </div>
            <div className={classes.tags}>
              {(data?.properties?.tags || '').split(' ').map((tag: string) => <Chip key={tag} label={tag}/>)}
            </div>
            <Box m="3rem auto" maxWidth="30rem">
              <ActivitiesPreviewImages activity={data} />
            </Box>
            
          </Grid>
          <Grid item md={6} lg={6} xl={6}>
          {
            data?.properties?.duration && 
              <Typography align="center"  variant="body1">
                {humanizeDuration(data.properties.duration)}
              </Typography>
          }
          {
            (data?.properties?.metaRequirements && data?.properties?.metaRequirements.length) ? <div className={classes.requirements}>
              <FormLabel>Requirements</FormLabel>
              <MaterialsList activity={data} />
            </div> : null
          }
          </Grid>
      </Grid>
    </Card>
    <ReactMarkdown
      source={data.description}
    />
  </div>
}