import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Check } from '@material-ui/icons';
import { Clear } from '@material-ui/icons';
import { ListItemText } from '@material-ui/core';
import { ListItemIcon } from '@material-ui/core';
import { MenuItem } from '@material-ui/core';
import { EventAvailableRounded } from '@material-ui/icons';


import IconMenu from '../../../../components/general/IconMenu';
import {
    addCompletedActivity,
    addDroppedActivity,
    addActivityToSchedule,
    removeActivityFromSchedule
} from '../utils';
import { addedActivitiesSelector } from '../../../../../utils/redux/config';


export default function AddActivityOptions({activity}: {activity: any}){
  const addedActivities = useSelector(addedActivitiesSelector);
  const dispatch = useDispatch();
  return <IconMenu
    renderMenuItems={(handleClose)=>{
      return [
        addedActivities[activity.id].inSchedule ? <MenuItem onClick={()=>{
          dispatch(removeActivityFromSchedule(activity))
          handleClose()
        }}>
        <ListItemIcon>
          <EventAvailableRounded color="secondary" fontSize="small" />
        </ListItemIcon>
      <ListItemText primary="Remove From Schedule" />
    </MenuItem> : <MenuItem onClick={()=>{
          dispatch(addActivityToSchedule(activity))
          handleClose()
        }}>
        <ListItemIcon>
          <EventAvailableRounded color="primary" fontSize="small" />
        </ListItemIcon>
      <ListItemText primary="Add To Schedule" />
    </MenuItem>,
    <MenuItem onClick={()=>{
      dispatch(addCompletedActivity(activity))
      handleClose()
    }}>
        <ListItemIcon>
          <Check color="primary" fontSize="small" />
        </ListItemIcon>
      <ListItemText primary="Done" />
      </MenuItem>,
        <MenuItem onClick={()=>{
          dispatch(addDroppedActivity(activity))
          handleClose() 
        }}><ListItemIcon>
            <Clear color="secondary" fontSize="small" />
            </ListItemIcon>
          <ListItemText primary="Remove" />
        </MenuItem>
      ]
    }}
  />
}