export const removeActivity = (activity: any)=>(dispatch: Function, getState: Function)=>{
  const nextAddedActivities = blacklist(configSelector(getState()).addedActivities, activity.id);
  // updateLocalAddedActivities(nextAddedActivities);

  dispatch(createConfigAction({
    addedActivities: nextAddedActivities,
  }))
}

