export const purgeActivities = (activities: any)=>(dispatch: Function, getState: Function)=>{
  const { addedActivities } = configSelector(getState());
  const nextAddedActivities = activities.reduce((acc: any, curr: any)=>{
    acc[curr.id] = addedActivities[curr.id]
    return acc;
  }, {});
  dispatch(createConfigAction({
    addedActivities: nextAddedActivities
  }))
  // updateLocalAddedActivities(nextAddedActivities);  
}

export const updateActivities = (activityRelationships: any)=>(dispatch: Function, getState: Function)=>{
  const nextAddedActivities = activityRelationships.reduce((acc: any, curr: any)=>{
    acc[curr.id].ordinal=curr.ordinal
    return acc;
  }, {...configSelector(getState()).addedActivities});
  dispatch(createConfigAction({
    addedActivities: nextAddedActivities
  }));
  // updateLocalAddedActivities(nextAddedActivities);
}

export const addActivityToSchedule = (activity: any)=>(dispatch: Function, getState: Function)=>{
  const { addedActivities } = configSelector(getState());
  const highestOrdinal = Object.values(addedActivities).reduce((acc: number, curr: any)=>{
    if(curr.inSchedule && curr.ordinal > acc){
      return curr.ordinal
    }
    return acc
  }, 0)
  const nextAddedActivities = {
    ...addedActivities,
    [activity.id]: {
      ...(addedActivities[activity.id] || {}),
      inSchedule: true,
      ordinal: highestOrdinal+1,
    }
  };
  dispatch(createConfigAction({
    addedActivities: nextAddedActivities
  }));
  // updateLocalAddedActivities(nextAddedActivities);
}

export const removeActivityFromSchedule = (activity: any)=>(dispatch: Function, getState: Function)=>{
  const { addedActivities } = configSelector(getState());
  const nextAddedActivities = {
    ...addedActivities,
    [activity.id]: {
      ...addedActivities[activity.id],
      inSchedule: false,
    }
  };
  dispatch(createConfigAction({
    addedActivities: nextAddedActivities
  }));
  // updateLocalAddedActivities(nextAddedActivities);
}

