import React from 'react';
import { TimelineOppositeContent } from '@material-ui/lab';
import Timeline from '@material-ui/lab/Timeline';
import TimelineItem from '@material-ui/lab/TimelineItem';
import TimelineSeparator from '@material-ui/lab/TimelineSeparator';
import TimelineConnector from '@material-ui/lab/TimelineConnector';
import TimelineContent from '@material-ui/lab/TimelineContent';
import TimelineDot from '@material-ui/lab/TimelineDot';
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import reorderOrdinals from 'reorder-ordinals';
import { Link } from 'react-router-dom';
import AddActivityOptions from '../Options';

const grid = 8;

const getListStyle = () => ({
  // background: isDraggingOver ? "lightblue" : "lightgrey",
  // padding: grid,
  // width: 250
});


const getItemStyle = (isDragging: any, draggableStyle: any) => ({
  // some basic styles to make the items look a bit nicer
  userSelect: "none",
  padding: grid * 2,
  margin: `0 0 ${grid}px 0`,

  // change background colour if dragging
  // background: isDragging ? "lightgreen" : "grey",

  // styles we need to apply on draggables
  ...draggableStyle
});


export default function DayTimeline(props: any) {
  const onDragEnd = (result: any) => {
    // dropped outside the list
    if (!result.destination) {
      return;
    }
    const nextData = reorderOrdinals()(props.data, result.source.index, result.destination.index)
    props.mutate(
      nextData
    );
  }
  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <Droppable droppableId="droppable">
      {
        (provided, snapshot: any) => (
        <div
          {...provided.droppableProps}
          ref={provided.innerRef}
          style={getListStyle()}
        >
          <Timeline align="alternate">
            {
              (props.data || []).sort((a: any, b: any)=>a.ordinal - b.ordinal).map((activityRel: any, index: number) => {

                const activity = activityRel.activity;
                return <Draggable key={activityRel.id} draggableId={activityRel.id} index={activityRel.ordinal}>
                {
                  (provided, snapshot) => (<span
                    ref={provided.innerRef}
                    {...provided.draggableProps}
                    {...provided.dragHandleProps}
                    style={getItemStyle(
                      snapshot.isDragging,
                      provided.draggableProps.style
                    )}
                  >
                    <TimelineItem>
                      <TimelineOppositeContent>
                        <AddActivityOptions activity={activity} />
                      </TimelineOppositeContent>
                      <TimelineSeparator>
                        <TimelineDot />
                        {index === (props.data.length - 1) ? null : <TimelineConnector /> }
                      </TimelineSeparator>
                      <TimelineContent>
                        <Link to={`/shedules/activities/${activity.id}`}>{activity.name}</Link>
                      </TimelineContent>
                    </TimelineItem>
                  </span>)
                }
                </Draggable>
              })
            }
          </Timeline>
        </div>)
      }
      </Droppable>
    </DragDropContext>
  );
}