import React, { useEffect, useState } from 'react';
import { useCustomSwr, SERVER_ADDRESS } from '../../../../utils/remote';

import _ from 'lodash';

import SearchActivitiesNonSearchRow from '../NonSearchRow';
import LazyLoad from 'react-lazyload';
import SearchActivitiesNonSearchPortraitRow from '../NonSearchPortraitRow';
import SearchActivitiesNonSearchSquareRow from '../NonSearchSquareRow';

export default function ListActivities({subjects, ages}: {subjects: string[], ages: string[]}){
  const [tagOrder, setTagOrder] = useState<string[]>([]);
  const defaultSubjects = subjects;
  const { data: popularTags } : {data: string[]} = useCustomSwr(SERVER_ADDRESS+'/tags/popular', {
    cache: true,
    defaultValue: []
  });

  useEffect(()=>{
    const defaultSubjectHash = defaultSubjects.reduce((acc: any, curr: string)=>{
      acc[curr]=true
      return acc;
    }, {})
    setTagOrder(_.sampleSize(defaultSubjects, defaultSubjects.length)
      .concat(popularTags.filter((tag: string)=>(tag.indexOf('age') === -1))
      .filter((tag: string)=>!defaultSubjectHash[tag]))
    );
  }, [defaultSubjects, ages, popularTags])

  const squareRender = (tag: string)=>{
    return <div style={{minHeight: '300px', minWidth: '100%'}}><LazyLoad height={200} key={tag}>
      <SearchActivitiesNonSearchSquareRow tag={tag} ages={ages} />
    </LazyLoad></div>;
  }

  const portraitRender = (tag: string)=>{
    return <div style={{minHeight: '300px', minWidth: '100%'}}><LazyLoad height={200} key={tag}>
      <SearchActivitiesNonSearchPortraitRow tag={tag} ages={ages} />
    </LazyLoad></div>;
  }
  
  const render = (tag: string)=>{
    return <div style={{minHeight: '300px', minWidth: '100%'}}><LazyLoad height={300} key={tag}>
      <SearchActivitiesNonSearchRow tag={tag} ages={ages} />
    </LazyLoad></div>;
  }
  return <div>
    {
      tagOrder.map((tag: string, i: number)=>{
        if(i === 0 || i === 8) return squareRender(tag);
        if(i === 3 || i === 6 || i === 12) return portraitRender(tag)
        return render(tag)
      })
    }

  </div>
}