import React from 'react';
import { makeStyles } from '@material-ui/core';
import Slideshow from '../../../../components/general/Slideshow';

const useStyles = makeStyles(theme => ({
  img: {
    width: '100%',
    height: '30rem',
    backgroundSize: 'cover !important',
  }
}));

export default function ActivitiesPreviewImages({activity, className}: {activity: any, className?: string}){
  const classes = useStyles();
  return <Slideshow images={activity.properties.imgLinks.map((img: any) => <div className={classes.img} style={{'background': `url(${img.src}) no-repeat center center`}}></div>)}/>
}

