import React from 'react';

import { useSelector, useDispatch } from 'react-redux';
import { dashboardTabsSelector } from '../../../utils/redux/config';
import { uiSelector, createUIAction } from '../../../utils/redux/ui';
import Config from './Config';
import MyTabs, { TabPanel } from '../../components/general/Tabs';
import ListActivities from './ListActivities';
import FAIcon from '../../components/general/FAIcon';

import {IconButton } from '@material-ui/core';

// const orderedDashboardTabs = createSelector(dashboardTabsSelector, (dashboardTabs)=>[...dashboardTabs].sort((a,b)=>a.ordinal-b.ordinal))

export default function Dashboard(){
  const rawDashboardTabs = useSelector(dashboardTabsSelector);
  const dashboardTabs = [...rawDashboardTabs].sort((a,b)=>a.ordinal-b.ordinal)
  // const dashboardTabs = useSelector(orderedDashboardTabs);
  const { dashboard } = useSelector(uiSelector);
  const dispatch = useDispatch();
  const onChange = (tab: number, isEditing?: Boolean)=>dispatch(createUIAction({
    dashboard: {
      tab,
      isEditing: isEditing || false
    }}))
  return <div>
    <MyTabs
      tabsProps={{variant: "standard"}}
      value={dashboard.tab}
      onChange={(tab)=>onChange(tab)}
      tabPropsArr={dashboardTabs.map(d=>({
        label: <span>{d.name || d.ages.join(' & ') || `Tab ${d.ordinal+1}`}
        <IconButton
          onClick={(e)=>{
            e.stopPropagation();
            onChange(d.ordinal, true)
          }}
        >
          <FAIcon type="pen" size="1x" />
        </IconButton>
      </span>
      }))}
    />
    {
      dashboardTabs.map((d, i)=><TabPanel key={d.ordinal} value={dashboard.tab} index={i}>
        {
          (!d.ages.length || dashboard.isEditing) ? <Config ordinal={d.ordinal} /> : <ListActivities ages={d.ages} subjects={d.subjects}/>
        }
        </TabPanel>)
    }
    

  </div>
}