import React, {useState} from 'react';
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import IconButton from '@material-ui/core/IconButton';
import AccountCircle from '@material-ui/icons/AccountCircle';
import { useHistory } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { createUserAction } from '../../../../../utils/redux/user';


export default function Profile({className}: {className: string}){
  const [anchorEl, setAnchorEl] = useState<HTMLElement|null>(null);
  const dispatch = useDispatch();
  const history = useHistory();
  const handleClose = ()=>{
    setAnchorEl(null);
  }
  const handleClick = (event: any) => {
    setAnchorEl(event.currentTarget);
  };
  return <>
    <IconButton className={className} color="inherit" onClick={handleClick} aria-label="open profile menu" component="span">
      <AccountCircle />
    </IconButton>
      <Menu
      id="profile-menu"
      anchorEl={anchorEl}
      keepMounted
      open={Boolean(anchorEl)}
      onClose={handleClose}
    >
      <MenuItem onClick={()=>{
        handleClose()
        history.push('/config')
      }}>Settings</MenuItem>
      
      <MenuItem onClick={()=>{
        handleClose()
        dispatch(createUserAction(null))
        history.push('/')
      }}>Logout</MenuItem>
    </Menu>
  </>
}