export const getMaterialId = (id: string, req: any)=>id+req.raw

export const materialTypeNotMaterial = (materials: any, materialTypes: any)=>(id: string, req: any)=>{
  return !materials[getMaterialId(id, req)] && materialTypeMatch(materialTypes)(req.types)
}

export const materialTypeMatch = (materialTypes: any)=>(types: any)=>types.reduce((acc: any, curr: any)=>{
  if(acc) return acc;
  return materialTypes[curr];
}, false);