import React from 'react';
import { makeStyles } from '@material-ui/core';
import SheduleSlot from './Slot';
import PortraitSlot from './PortraitSlot';
import SquareSlot from './SquareSlot';

type $props = {
  wrap?: Boolean,
  addedActivityPage?: Boolean,
  data: any[],
  featuredPortrait?: Boolean,
  featuredSquare?: Boolean
}
const useStyles = makeStyles({
  root: (props: {wrap: Boolean}) => ({
    marginTop: '1rem',
    whiteSpace: props.wrap ? 'normal' : 'nowrap',
    // whiteSpace: 'normal',
    overflowX: 'scroll',
  }),
});

function SheduleRow({ addedActivityPage, wrap, data, featuredPortrait, featuredSquare }: $props){  
  const classes = useStyles({wrap: !!(addedActivityPage || wrap)});
  
  const Slot = featuredPortrait ? PortraitSlot : featuredSquare ? SquareSlot : SheduleSlot;
  return <div className={classes.root}>
    {
      data.map((activity, i)=>{
        return <Slot addedActivityPage={addedActivityPage} key={activity.id} shedule={activity} />
      })
    }
  </div>
}

export default SheduleRow