import jwt from 'jsonwebtoken';
import { getToken } from '../../auth';

export const DEFAULT_USER_TYPE = 'DEFAULT_USER_TYPE';

const token = getToken();

const defaultState = token ? jwt.decode(token) : null

export const reducer = (state=defaultState, action: any)=>{
    if(action.type === DEFAULT_USER_TYPE) return action.payload
    return state;
}

export const createUserAction = (payload: any)=>{
  return {
    type: DEFAULT_USER_TYPE,
    payload,
  }
}



export const userSelector = (state: any)=>state.user