import React, { useState, useEffect } from 'react';
import { Typography, Divider, makeStyles } from '@material-ui/core';

import { useCustomSwr, SERVER_ADDRESS } from '../../../../utils/remote';
import SheduleRow from '../../../components/general/SheduleRow';
import { blue } from '@material-ui/core/colors';

const useStyles = makeStyles({
  header: {
    marginTop: '1rem',
    color: blue[700]
  }
})

export default function SearchActivitiesNonSearchRow({tag, ages, portraitFeatured = false}: {tag: string, ages: string[], portraitFeatured?: Boolean}){
  const classes = useStyles();
  const [ previousSizes, setPreviousSizes ] = useState({});
  const defaultTags =  ages;
  
  const tags = defaultTags.concat([tag]).map((t: string)=>'#'+t).reverse().join(' ');
  const { data, page } = useCustomSwr(SERVER_ADDRESS+`/shedules/activities/public`, { 
    defaultValue: [],
    page: true,
    query: {
      tags,
      portraitFeatured
    }, 
    deps: [],
    cache: true
  });
  useEffect(()=>{
    setPreviousSizes({
      ...previousSizes,
      [page]: data.length
    });
  }, [data]);
  
  return <div>
    <Typography align="center" variant="h6" component="h3" className={classes.header}>{'#'+tag}</Typography>
    {
      !data.length ? <p>No Results</p> : <SheduleRow data={data} />
    }
    <Divider variant="inset" />
  </div>
}