import React from 'react';
import { Card, makeStyles, CardMedia } from '@material-ui/core';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';

import Tags from '../Tags';
import {     addedActivitySelector,
  wontDoActivitySelector,
  completedActivitySelector } from '../../../../../utils/redux/config';

type $props = {
  shedule: any,
  addedActivityPage?: Boolean
}
const useStyles = makeStyles((theme)=>({
  root: {
    display: "inline-block",
    width: "400px",
    verticalAlign: 'top',
    marginRight: '1rem',
    marginBottom: '1rem',
  },
  img: {
    height: '400px'
  }
}));

const SquareSlot = React.memo(function SquareSlot(props: $props){
  const activity = props.shedule;
  const addedActivity = useSelector(addedActivitySelector(activity.id));
  const wontDoActivity = useSelector(wontDoActivitySelector(activity.id));
  const completedActivity = useSelector(completedActivitySelector(activity.id));

  const classes = useStyles();
  if(completedActivity || wontDoActivity) return null;
  if(!props.addedActivityPage && addedActivity) return null;
  

  const isSquareImg = (img: any) => {
    const quotient = img.height / img.width;
    return 0.85 < quotient && quotient < 1.15
  }

  const activityLinkTo = "/shedules/activities/"+activity.id;
  return <Card key={activity.id} className={classes.root}>
    <Link to={activityLinkTo}>
      <CardMedia title={activity.name} className={classes.img} image={activity.properties.imgLinks.find(isSquareImg).src}/>
    </Link>
    <Tags activity={activity} direction="row" width="400px" height="55px"/>
  </Card>
});


export default SquareSlot