import React, { useState, useEffect } from 'react';
import { Divider } from '@material-ui/core';

import { useCustomSwr, SERVER_ADDRESS } from '../../../../utils/remote';
import SheduleRow from '../../../components/general/SheduleRow';


export default function SearchActivitiesNonSearchSquareRow({tag, ages}: {tag: string, ages: string[]}){
  const [ previousSizes, setPreviousSizes ] = useState({});
  const defaultTags =  ages;
  
  const tags = defaultTags.concat([tag]).map((t: string)=>'#'+t).reverse().join(' ');
  const { data, page } = useCustomSwr(SERVER_ADDRESS+`/shedules/activities/public`, { 
    defaultValue: [],
    page: true,
    query: {
      tags,
      featuredSquare: true
    }, 
    deps: [],
    cache: true
  });
  useEffect(()=>{
    setPreviousSizes({
      ...previousSizes,
      [page]: data.length
    });
  }, [data]);
  
  return <div>
    {
      // <Typography align="center" variant="h6" component="h3" className={classes.header}>{'#'+tag}</Typography>
    }
    {
      !data.length ? <p>No Results</p> : <SheduleRow featuredSquare data={data}/>
    }
    <Divider variant="inset" />
  </div>
}