import React, { useState } from 'react';
import _, { debounce } from 'lodash';
import { Dialog, Typography, makeStyles, AppBar, Toolbar, Button, Divider, Card, CardContent, TextField } from '@material-ui/core';
import blacklist from 'blacklist';
import ReactMarkdown from 'react-markdown';
import classnames from 'classnames';
import { useSelector, useDispatch } from 'react-redux';
import { v4 } from 'uuid';
import {
    createConfigAction,
    dashboardTabsSelector
} from '../../../../utils/redux/config';
import FAIcon from '../../../components/general/FAIcon';
import MyPopover from '../../../components/general/Popover';
import { createUIAction, uiSelector } from '../../../../utils/redux/ui';


interface $props {
  ordinal: number,
};

export default function Config({ordinal}: $props){
  const dashboardTabs = useSelector(dashboardTabsSelector);
  const { dashboard } = useSelector(uiSelector);
  const dispatch = useDispatch();
  const dashboardTab = dashboardTabs.find(x=>x.ordinal===ordinal);
  const onChange = (key: string, value: any)=>{
    dispatch(createConfigAction({dashboardTabs: [
      {
        ...dashboardTab,
        [key]: value,
        touched: true,
      },
     ...dashboardTabs.filter(x=>x.ordinal !== ordinal),
     ...(dashboardTab && !dashboardTab.touched ? [{
       ordinal: dashboardTabs.length,
       ages: [],
       subjects: [],
       uuid: v4(),
     }] : []),
    ]}))
  }
  const debouncedOnChange = debounce((value)=>{
    onChange('name', value)
  }, 500)
  
  if(!dashboardTab) return null;
  return <div>
    <TextField label="Tab Name" placeholder={`Tab ${ordinal+1}`} defaultValue={dashboardTab.name} onChange={(event)=>{
      debouncedOnChange(event.target.value)
    }} />
    <Divider variant="inset"/>
    <PickAges key={dashboardTab.uuid} values={dashboardTab.ages} onSave={(nextAges: string[])=>onChange('ages', nextAges)} color={!dashboardTab.ages.length ? 'secondary' : undefined} className="pick-ages" />
    <Divider variant="inset"/>
    <PickSubjects values={dashboardTab.subjects} onSave={(nextSubjects: string[])=>onChange('subjects', nextSubjects)} color={!dashboardTab.subjects.length ? 'secondary' : undefined} className="pick-subjects" />
    <Divider variant="inset"/>
    {
      !dashboardTab.ages.length ? null : <Button onClick={()=>{
        dispatch(createUIAction({
          dashboard: {
            ...dashboard,
            isEditing: false
          }
        }))
      }}>Done</Button>
    }
    {
      dashboardTabs.length === 1 || !dashboardTab.touched ? null : <Button onClick={()=>{
        dispatch(createConfigAction({
          dashboardTabs: [
            ...dashboardTabs.filter(x=>x.ordinal !== ordinal).map(x=>({...x, ordinal: x.ordinal > ordinal ? x.ordinal-1 : x.ordinal}))
          ]
        }))
        dispatch(createUIAction({
          dashboard: {
            ...dashboard,
            isEditing: false
          }
        }))
      }}
      color="secondary">Delete</Button>
    }
  </div>
}

