const useStyle = makeStyles({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    alignContent: 'flex-start',
  },
  title: {
    fontSize: '1rem',
  },
  defaultAges: {
    marginTop: '1rem',
    fontSize: '1.1rem',
  }
})

interface $pickDefaultProps {
  values: string[],
  onSave: (a: string[])=>void,
  color?: 'secondary',
  className: string,
  modalTitle: string,
  selectStatement: string,
  tags: any[],
  defaultButton: string
}

function PickDefault({tags, values, onSave, color, className, modalTitle, selectStatement, defaultButton}: $pickDefaultProps){
  const classes = useStyle();
  
  const [ selected, select] = useState<{[id: string]: Boolean}>(values.reduce((acc: any, curr)=>{
    acc[curr] = true;
    return acc;
  }, {}));
  
  const toggle = (tag: string)=>{
    if(selected[tag]){
      select(blacklist(selected, tag));
    } else {
      select({...selected, [tag]: true});
    }
  }
  return <Modal
    onSave={()=>onSave(Object.keys(selected))}
    renderContent={
      (handleClickOpen: any)=><>
      <Typography variant="h3" className={classes.defaultAges}>{modalTitle}</Typography>
      <Button
        variant="contained"
        color={color}
        className={className}
        onClick={handleClickOpen}
        >{
        Object.keys(selected).length ? Object.keys(selected).map((x)=>'#'+x).join(' ') : defaultButton
        }</Button>
      </>
    }
    renderDialogContent={()=><>
      <Typography component="h2" className={classes.title}>{
        selectStatement
      }</Typography>
      <div className={classes.root}>
        {
          tags.map((tag, i)=>{
            const isSelected = selected[tag.name];
            return <Tile key={i} toggle={toggle} isSelected={isSelected} tag={tag} i={i} />
          })
        }
      </div>
      </>}
  />
}

const useTileStyles = makeStyles({
  tile: ({isSelected, tag}: any)=>({
    display: 'inline-block',
    width: '220px',
    height: '200px',
    marginTop: '5px',
    marginRight: '5px',
    cursor: 'pointer',
    position: 'relative',
    ...(isSelected ? { backgroundColor: 'gray', color: '#fff' } : {
      backgroundColor: tag.color,
    })
  }),
  iconTextWrapper: {
    // width: 'fit-content'
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '205px',
  },
  tileHash: {
    position: 'absolute',
    bottom: '5%',
    left: '5%',
  },
})

const usePopoverStyles = makeStyles((theme) => ({
  popover: {
    pointerEvents: 'none',
  },
  paper: {
    padding: theme.spacing(1),
  },
}));

function Tile({tag, i, isSelected, toggle}: any){
  const classes = useTileStyles({tag, isSelected})
  const popoverClasses = usePopoverStyles();
  return <MyPopover
    popoverProps={{className: popoverClasses.popover}}
    popoverClasses={{paper: popoverClasses.paper}}
    className={classes.tile}
    disableHover={Boolean(tag.description)}
    renderHoverableContent={
      ()=><>
        <div
          className={classes.iconTextWrapper}
          onClick={()=>toggle(tag.name)}
        >
          
          {
            tag.icon ? <>
            <FAIcon raw={tag.icon} size="5x" />
            {
              tag.sideContent && <Typography variant="body2" component="h3">{tag.sideContent}</Typography>
            }
            </> : <Typography variant="body2" component="h3">{tag.monogram}</Typography>

          }
        </div>
        <Typography
              className={classnames(classes.tileHash, {
                'tag-name': !i
              })}
              variant="body1"
            >{`#${tag.name}`}</Typography>
      </>
    }
    renderOnHoverContent={
      ()=><ReactMarkdown
      source={tag.description}
    />
    }
  />
}

function Modal(props: any){
  const [open, setOpen] = useState(false);
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = (e: any) => {
    e.stopPropagation();
    setOpen(false);
    props.onSave && props.onSave()
  };

  return <div>
  <Card>
    <CardContent>
      {
        props.renderContent(handleClickOpen)
      }
    </CardContent>
  </Card>
  <Dialog fullScreen open={open} onClose={handleClose}>
    <AppBar>
      <Toolbar>
        <Button autoFocus color="inherit" onClick={handleClose}>
          save
        </Button>
      </Toolbar>
    </AppBar>
    <div>
      {
        props.renderDialogContent()
      }
      <Button style={{height: '4rem'}} variant="contained" fullWidth color="primary" onClick={handleClose}>Save</Button>
    </div>
  </Dialog>
  </div>
}

