import React from 'react';
import {Typography, Tooltip, Card, CardHeader, makeStyles } from '@material-ui/core';
import _ from 'lodash';
import { Link } from 'react-router-dom';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import StarRatings from 'react-star-ratings';
import { useSelector } from 'react-redux';
import LinesEllipsis from 'react-lines-ellipsis'
import blue from '@material-ui/core/colors/blue';
import { amber } from '@material-ui/core/colors';
import { CardContent } from '@material-ui/core';

import FAIcon from '../../FAIcon';
import AddActivityButtons from '../../../../shedules/activities/added/Button';
import MaterialsTag from '../../../../materials/Tag';
import AddActivityOptions from '../../../../shedules/activities/added/Options';
import MyPopover from '../../Popover';
import ActivitiesPreviewImages from '../../../../shedules/activities/preview/Images';
import {     addedActivitySelector,
  wontDoActivitySelector,
  completedActivitySelector } from '../../../../../utils/redux/config';

const useActivityButtonStyles = makeStyles({
  root: {
    display: 'block',
    padding: '5px',
  }
})

const useStyles = makeStyles((theme)=>({
  activityRoot: {
    display: "inline-block",
    width: "280px",
    verticalAlign: 'top',
    marginRight: '1rem',
    marginBottom: '1rem',
    // '&:hover $addActivity': {
    //   height: '50px'
    // } 

  },
  tagRoot: {
    display: 'flex',
    flexDirection: 'column',
    width: '280px',
    height: '150px',
    flexWrap: 'wrap',
    alignItems: 'flex-start',
    justifyContent: 'flex-start',
    alignContent: 'flex-start',
    padding: 0,
    paddingBottom: '0 !important',
    
    overflow: 'hidden',
  },
  tag: {
    display: 'inline-block',
    width: '78px',
    height: '48px',
    marginRight: '2px',
    marginBottom: '2px',
    cursor: 'pointer',
  },
  imageTag: {
    position: 'relative',
    overflow: 'hidden',
    width: '120px',
    height: '150px',
  },
  activityTitle: {
    whiteSpace: 'normal',
    textDecoration: 'none',
    height: '3rem',
    // overflow: 'hidden',
  },
  sheduleTitle: {
    fontSize: '1rem',
    color: 'rgba(0, 0, 0, 0.87)',
    textDecoration: 'none',
    fontWeight: 500,
  },
  sideContent: {
    display: 'inline-block',
    verticalAlign: 'super',
  },
  starSideContent: {
    display: 'block',
    textAlign: 'center',
  },

  tagContent: {
    width: 'fit-content',
    margin: '0 auto',
    marginTop: '7px',
  },
  activityImage: {
    top: '50%',
    width: '100%',
    position: 'relative',
    transform: 'translateY(-50%)',
  },
  headerRoot: {
    borderBottom: '#b4b0b0 solid 1px',
  },

}));

const usePopoverStyles = makeStyles(theme => ({
  paper: {
    width: '350px',
    height: '300px',
  }
}));

type $props = any

const SheduleSlot = React.memo(function SheduleSlot(props: $props){
  
  const activityButtonClasses = useActivityButtonStyles()
  const activity = props.shedule;
  const addedActivity = useSelector(addedActivitySelector(activity.id));
  const wontDoActivity = useSelector(wontDoActivitySelector(activity.id));
  const completedActivity = useSelector(completedActivitySelector(activity.id));

  const classes = useStyles();
  const popoverClasses = usePopoverStyles();
  if(completedActivity || wontDoActivity) return null;
  if(!props.addedActivityPage && addedActivity) return null;
  const uniqueTags: any[] = _.uniqBy(activity.tags, 'id')
  const tags: any[] = uniqueTags.filter((tag: any)=>(tag.name.indexOf('age') === -1));
  const [minAge, maxAge] = uniqueTags.filter((tag: any)=>(tag.name.indexOf('age') > -1)).reduce((acc: any, curr: any)=>{
    const age = curr.name.split('-')[1];
    if(!acc[0]){
      acc[0] = age;
    } 
    if(acc[0] > age){
      acc[0] = age;
    }
    if(!acc[1]){
      acc[1] = age;
    } 
    if(acc[1] < age){
      acc[1] = age;
    }
    return acc;
  }, [undefined, undefined])

  const activityLinkTo = "/shedules/activities/"+activity.id;

  return <Card key={activity.id} className={classes.activityRoot}>
    <CardHeader
      classes={
        {
          root: classes.headerRoot
        }
      }
      title={
        <Tooltip title={activity.name}><div className={classes.activityTitle}>
          <Link className={classes.sheduleTitle} to={activityLinkTo}>
            <Typography component="h3" className={classes.sheduleTitle}>
              <LinesEllipsis
              text={activity.name}
              maxLine='2'
              ellipsis='...'
              basedOn='letters'
              />
            </Typography>
          </Link>
        </div>
        </Tooltip>
      }
      action={props.addedActivityPage ? <AddActivityOptions activity={activity} />
      : <>
        <AddActivityButtons classes={activityButtonClasses} activity={activity} /> 
        <AddActivityButtons directToSchedule classes={activityButtonClasses} activity={activity} /> 
      </>
      }
    />
    

    <CardContent className={classes.tagRoot}>
      {
        activity.shares ? <div style={{color: blue[700] }}  className={classes.tag}>
          
        {

          // <Tooltip title={`This activity has been shared on facebook twitter and pinterest a combined ${activity.shares} times`}>
          <Tooltip title={`This activity has a rating of ${activity.shares/10} out of 5`}>
            <div className={classes.tagContent}>
              {
                // <FAIcon size="2x" outline type="star" />
                <span><StarRatings
                  rating={activity.shares/10}
                  starDimension="10px"
                  starSpacing="2px"
                  starRatedColor={amber[700]}
                />
                </span>
              }
              {
                // <Typography className={classes.sideContent}>{numab(activity.shares, 1)}</Typography>
              }
              <Typography className={classes.starSideContent}>{activity.shares/10}</Typography>
            </div>
          </Tooltip>
        }
        </div> : null
      }
        {
          maxAge === undefined ? null : <div className={classes.tag}>
            <Tooltip title={`For kids ${minAge} to ${maxAge} years old`}>
              <div className={classes.tagContent}
              >
                <FAIcon size="2x" outline type="grin" />
                <Typography className={classes.sideContent}>{`${minAge}${minAge === maxAge ? '' : `:${maxAge}Y`}`}</Typography>
              </div>
            </Tooltip>
          </div>
        }
        {
          (!activity.properties.metaRequirements || !activity.properties.metaRequirements.length) ? null : <MaterialsTag className={classes.tag} activity={activity}/>
        }
      {
        tags.slice(0, 2 + (maxAge === undefined ? 0 : 1)).map((tag: any, i: number) => {      
          return <div
          //  style={{backgroundColor: tag.color}}
            className={classes.tag} key={tag.id + i}>
          <Tooltip title={`#${tag.name}: ${tag.description}`}>
            <div className={classes.tagContent}>
              <FAIcon raw={tag.icon} size="2x" />
              {
                tag.sideContent && <Typography className={classes.sideContent}>{tag.sideContent}</Typography>
              }
            </div>
        </Tooltip>
            
          </div>
        })    
      }
      {
        
            <MyPopover
              disableHover={activity.properties.imgLinks.length < 2}
              popoverClasses={popoverClasses}
              popoverProps={{
                anchorOrigin: {
                  vertical: 'bottom',
                  horizontal: 'right',
                },
                transformOrigin: {
                  vertical: 'bottom',
                  horizontal: 'right',
                }
              }}
              renderOnHoverContent={
                ()=><ActivitiesPreviewImages activity={activity} />
              }
              renderHoverableContent={
                ()=><Link to={activityLinkTo}>
                  <div className={classes.imageTag}>
                    <LazyLoadImage 
                    height={150}
                    src={activity.image}
                    alt={activity.name}
                    className={classes.activityImage}
                    style={{
                      display: 'block',
                      marginLeft: 'auto',
                      marginRight: 'auto',

                    }}
                  />
                </div>
              </Link>
            }
          />
      }
    </CardContent>    
  </Card>     
});


export default SheduleSlot