import React from 'react';
import {Typography, Tooltip, makeStyles } from '@material-ui/core';
import _ from 'lodash';
import StarRatings from 'react-star-ratings';

import blue from '@material-ui/core/colors/blue';
import { amber } from '@material-ui/core/colors';
import { CardContent } from '@material-ui/core';

import FAIcon from '../../FAIcon';
import MaterialsTag from '../../../../materials/Tag';

type $props = {
  activity: any
} & $styleProps;

type $styleProps = {
  direction?: any,
  width?: string,
  height?: string,
}

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: (props: $styleProps)=>props.direction || 'column',
    width: (props: $styleProps)=>props.width || '280px' ,
    height: (props: $styleProps)=>props.height || '150px',
    flexWrap: 'wrap',
    alignItems: 'flex-start',
    justifyContent: 'flex-start',
    alignContent: 'flex-start',
    padding: 0,
    paddingBottom: '0 !important',
    overflow: 'hidden',
  },
  tag: {
    display: 'inline-block',
    width: '78px',
    height: '48px',
    marginRight: '2px',
    marginBottom: '2px',
    cursor: 'pointer',
  },
  sideContent: {
    display: 'inline-block',
    verticalAlign: 'super',
  },
  starSideContent: {
    display: 'block',
    textAlign: 'center',
  },
  tagContent: {
    width: 'fit-content',
    margin: '0 auto',
    marginTop: '7px',
  },
});

const Tags = React.memo(function Tags({activity, ...styleProps}: $props){
  const classes = useStyles(styleProps);
  const uniqueTags : any[] = _.uniqBy(activity.tags, 'id')
  const tags: any[] = uniqueTags.filter((tag: any)=>(tag.name.indexOf('age') === -1));
  const [minAge, maxAge] = uniqueTags.filter(tag=>(tag.name.indexOf('age') > -1)).reduce((acc, curr)=>{
    const age = curr.name.split('-')[1];
    if(!acc[0]){
      acc[0] = age;
    } 
    if(acc[0] > age){
      acc[0] = age;
    }
    if(!acc[1]){
      acc[1] = age;
    } 
    if(acc[1] < age){
      acc[1] = age;
    }
    return acc;
  }, [undefined, undefined])
  return <CardContent className={classes.root}>
    {
      activity.shares ? <Tag style={{color: blue[700] }}  tooltip={`This activity has a rating of ${activity.shares/10} out of 5`}>
        {
          // <FAIcon size="2x" outline type="star" />
          <span><StarRatings
            rating={activity.shares/10}
            starDimension="10px"
            starSpacing="2px"
            starRatedColor={amber[700]}
          />
          </span>
        }
        {
          // <Typography className={classes.sideContent}>{numab(activity.shares, 1)}</Typography>
        }
        <Typography className={classes.starSideContent}>{activity.shares/10}</Typography>
      </Tag>
      : null
    }
    {
      maxAge === undefined ? null : <Tag tooltip={`For kids ${minAge} to ${maxAge} years old`} iconProps={{outline: true, type: 'grin'}} sideContent={`${minAge}${minAge === maxAge ? '' : `:${maxAge}Y`}`} />
    }
    {
      (!activity.properties.metaRequirements || !activity.properties.metaRequirements.length) ? null : <MaterialsTag className={classes.tag} activity={activity}/>
    }
    {
      tags.slice(0, 2 + (maxAge === undefined ? 0 : 1)).map((tag, i) => {
        return <Tag key={i} tooltip={`#${tag.name}: ${tag.description}`} iconProps={{raw: tag.icon}} sideContent={tag.sideContent} />
      })    
    }
  </CardContent>  
});


export default Tags


type $tagProps = {
  style?: Object,
  iconProps?: Object,
  tooltip: string,
  children?: any,
  sideContent?: string
}

function Tag({style, tooltip, children, iconProps, sideContent}: $tagProps){
  const classes = useStyles();
  return <Tooltip title={tooltip}>
    <div style={style} className={classes.tag}>
      <div className={classes.tagContent}>
      {
        iconProps ? <FAIcon size="2x" {...iconProps} /> : null
      }
      {
        sideContent ? <Typography className={classes.sideContent}>{sideContent}</Typography> : null
      }
      {
        children
      }
      </div>
    </div>
  </Tooltip>
}