import React from 'react';
import { makeStyles, FormControl, FormLabel } from '@material-ui/core';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';

import MaterialsList from '../List';


import { ExpandMore as ExpandMoreIcon } from '@material-ui/icons';


const useStyles = makeStyles(theme=>({
  root: {
    margin: theme.spacing(3),
  },
}))

const useAccordionStyles = makeStyles({
  root: {
    height: '30px !important',
    minHeight: '30px !important'
  },
  content: {
    margin: '0 !important'
  }
})


export default function Accordions({data}: {data: any[]}){
  const classes = useStyles();
  const accordionClasses = useAccordionStyles();
  return <FormControl component="fieldset" className={classes.root}> 
  {
    data.map(a=>{
      return <Accordion defaultExpanded>
        <AccordionSummary
          classes={accordionClasses}
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
        <FormLabel component="legend">{a.name}</FormLabel>
        </AccordionSummary>
        <AccordionDetails>
          <MaterialsList activity={a} />
        </AccordionDetails>
      </Accordion>
    })
  }
</FormControl>
}