import React, { useState } from 'react';
import { Tabs, Tab, Grid, Typography } from '@material-ui/core';


import { useCustomSwr, SERVER_ADDRESS } from '../../../../utils/remote';
import SheduleRow from '../../../components/general/SheduleRow';
import { useSelector, useDispatch } from 'react-redux';

import DayTimeline from './Timeline';
import { updateActivities, purgeActivities } from './utils';
import Accordions from '../../../materials/Accordions';
import { addedActivitiesSelector } from '../../../../utils/redux/config';
import { uiSelector, createUIAction } from '../../../../utils/redux/ui';
import { useMediaQuery } from '@material-ui/core';
import MyTabs, { TabPanel } from '../../../components/general/Tabs';





export default function ShedulesAddedActivitiesShowMany(props: any){
  
  const addedActivities = useSelector(addedActivitiesSelector);
  const dispatch = useDispatch();
  const ui = useSelector(uiSelector);
  const [tab, setTab] = useState(ui.addedActivities.tab || 0);
  const defaultValue : any = []
  const { data } = useCustomSwr(SERVER_ADDRESS+`/shedules/activities/public`, { 
    defaultValue,
    query: {
      ids: Object.keys(addedActivities)
    }, 
    deps: [],
  });
  React.useEffect(()=>{
    if(data === defaultValue) return;
    if(data.length !== Object.keys(addedActivities).length){
      purgeActivities(data)
    }
  }, [data])
  const biggerThanSmall = useMediaQuery('(min-width:1000px)');
  const inScheduleActivities = data.filter((activity: any)=>addedActivities[activity.id].inSchedule);
  const schedule = !inScheduleActivities.length ? <p>Nothing in schedule</p> : <DayTimeline mutate={updateActivities} data={inScheduleActivities.map((activity: any) => ({
    id: activity.id,
    activity,
    ordinal: addedActivities[activity.id].ordinal
  }))} />
  const scheduleTitle = <Typography align="center">Schedule</Typography>
  return <div>
    <div>
      <MyTabs value={tab} onChange={(newValue: number)=>{
        setTab(newValue)
        dispatch(createUIAction({
          addedActivities: {
            tab: newValue
          }
        }))
      }} tabPropsArr={
        [
          {
            label: "Bookmarks"
          },
          ...(biggerThanSmall ? [] : [{
            label: "Schedule"
          }]),
          {
            label: "Materials"
          }
        ]
      } />
    </div>
    <TabPanel value={tab} index={0}>
      <Grid container>
        <Grid item sm={12} md={6}>
          {
            !data.length ? <p>No Results</p> : <SheduleRow addedActivityPage data={data} />
          }
        </Grid>
        {
          biggerThanSmall ? <Grid item md={6}>
            {scheduleTitle}
            {schedule}
          </Grid>
         : null
        }
        </Grid>
    </TabPanel>
    {
      biggerThanSmall ? null : <TabPanel value={tab} index={1}>
      {
        schedule
      }
    </TabPanel>
    }
      <TabPanel value={tab} index={biggerThanSmall ? 1 : 2}>
      <Grid container>
        {
          biggerThanSmall ? <Grid item md={6}>
            {scheduleTitle}
            {schedule}
          </Grid> : null
        }
        <Grid item sm={12} md={6}>
          {
            !inScheduleActivities.length ? <p>No activities in schedule</p> : <Accordions data={[...inScheduleActivities].sort((a,b)=>addedActivities[a.id].ordinal-addedActivities[b.id].ordinal)} />
          }
        </Grid>
      </Grid>
      </TabPanel>
    
  </div>
}