import React from 'react';
import { useSelector } from 'react-redux';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { IconButton, Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';
import Profile from './Profile';
import FAIcon from '../../general/FAIcon';
import { userSelector } from '../../../../utils/redux/user';
import { addedActivitiesSelector } from '../../../../utils/redux/config';
import HomeIcon from '@material-ui/icons/Home';
import { blue } from '@material-ui/core/colors';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    
    

  },
  logo: {
    height: '3.5rem',
  },
  titleLink: {
    flexGrow: 1,
    textDecoration: 'none',
    color: 'inherit',
    // marginRight: '3.5rem'
  },
  iconButtons: {
    // color: 'rgba(0,0,0,0.7)',
    color: blue[700],
    textDecoration: 'none'
  }
}));

export default ()=>{
  const classes = useStyles();
  const user = useSelector(userSelector);
  const addedActivities = useSelector(addedActivitiesSelector);

  return <AppBar position="sticky"
    color="inherit"
  >
    <Toolbar>
      <Link className={classes.titleLink} to="/">
        <Typography variant="h6" className={classes.title}>        
            <img alt="Acts logo" src="https://imgur.com/ltNSZaz.png" className={classes.logo}/>
        </Typography>
      </Link>
        
        {
          !user ? <>
          <Button color="inherit"><Link to="/login">Login</Link></Button>
          <Button color="inherit"><Link to="/signup">Signup</Link></Button></>
          :
          <>
          {
            // <Create />
          }
          <Link className={classes.iconButtons} to="/activities/added"><IconButton color="inherit">{Object.keys(addedActivities).length ? Object.keys(addedActivities).length : null}<FAIcon size="md" type="calendar-alt"/></IconButton></Link>
          <Link className={classes.iconButtons} to="/search"><IconButton color="inherit"><FAIcon size="md" type="search"/></IconButton></Link>
          <Link className={classes.iconButtons} to="/"><IconButton className="main-search-icon" color="inherit"><HomeIcon/></IconButton></Link>
          <Profile className={classes.iconButtons} />
          </>
        }
    </Toolbar>
  </AppBar>
}
