import React from 'react';
import { useCustomSwr, SERVER_ADDRESS } from '../../../utils/remote';


import SearchActivitiesRow from './Row';


export default function SearchActivities(){
  const { data: popularTags } = useCustomSwr(SERVER_ADDRESS+'/tags/popular', {
    cache: true
  });
  
  
  return <div>
    {
      <SearchActivitiesRow wrap popularTags={popularTags} />
    }
  </div>
}