import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { getMaterialId, materialTypeNotMaterial, materialTypeMatch } from '../utils';
import { createConfigAction, materialsSelector, materialTypesSelector } from '../../../utils/redux/config';
import { FormControlLabel, makeStyles } from '@material-ui/core';
import { FormGroup } from '@material-ui/core';
import { Checkbox } from '@material-ui/core';

const useStyle = makeStyles({
  typeMatch: {
    color: '#e0e0e0 !important',
  }
})



export default function MaterialsList({activity}: {activity: any}){
  const classes = useStyle();
  const materials = useSelector(materialsSelector);
  const materialTypes = useSelector(materialTypesSelector);


  const dispatch = useDispatch();
  
  const handleChange = (id: string, req: any)=>(event: any)=>{
    const checked = event.target.checked || materialTypeNotMaterial(materials, materialTypes)(id, req);
    const nextMaterials = {
      ...materials,
      [getMaterialId(id, req)]: checked
    }
    const nextMaterialTypes = req.types.reduce((acc: any, curr: string)=>{
      acc[curr] = checked;
      return acc
    }, {...materialTypes});
    dispatch(createConfigAction({
      materials: nextMaterials,
      materialTypes: nextMaterialTypes,
    }));
  }
  const a = activity;
  return  <FormGroup>
  {
    (a?.properties?.metaRequirements || []).map((req: any) => {
      return <FormControlLabel
        key={a.id+req.raw}
        control={
          <Checkbox
            classes={{
              checked: materialTypeNotMaterial(materials, materialTypes)(a.id, req) && classes.typeMatch
            }}
            checked={
              !!(materials[
                getMaterialId(a.id, req)
              ]) || 
              materialTypeMatch(materialTypes)(req.types) || 
              false
            }
            onChange={handleChange(a.id, req)} 
            name={req.raw} />
          }
        label={
          req.types.join(' or ')
          // req.raw
        }
      />
    })
  }
</FormGroup>
}
